*,
*::before,
*::after {
  box-sizing: border-box; }

body {
  margin: 0;
  padding: 0;
  font-size: 12px;
  color: #666;
  line-height: 1; }

.container {
  width: 300px;
  display: flex;
  flex-direction: column; }
  .container .header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 30px;
    padding: 0 10px;
    background: #f7f7f7;
    border-bottom: 1px solid #e6e6e6; }
    .container .header .name,
    .container .header .feedback {
      cursor: pointer; }
  .container .config {
    display: flex;
    flex-direction: column;
    position: relative;
    padding: 10px 10px 0; }
    .container .config .item {
      margin-bottom: 10px; }
      .container .config .item .key {
        margin-bottom: 5px; }
      .container .config .item .value input,
      .container .config .item .value select {
        width: 100%;
        height: 25px;
        padding: 0 5px;
        outline: none; }
  .container .debug {
    display: flex;
    flex-direction: column;
    padding: 10px 10px 0;
    height: 100px;
    overflow: auto;
    line-height: 1.4;
    background: #f7f7f7;
    border-top: 1px solid #e6e6e6; }
    .container .debug p {
      position: relative;
      margin: 0 0 5px; }
  .container .footer {
    height: 30px;
    cursor: pointer;
    color: #fff; }
    .container .footer .start {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      height: 100%;
      background: #4fc1e9; }
    .container .footer .stop {
      display: none;
      align-items: center;
      justify-content: center;
      width: 100%;
      height: 100%;
      background: #f6628f; }
  .container.recording .start {
    display: none; }
  .container.recording .stop {
    display: flex; }
